import React from 'react';

const Home = () => (
    <div style={styles.container}>
        <img 
            style={styles.imageCover}
            src="./images/bg.jpg"
            alt="bg"
        />
        <div style={styles.blockText}>
            <h1>SELAMAT DATANG</h1>
        </div>
    </div>
)

const styles = {
    container: {
        position: 'relative',
        textAlign: 'center',
    },
    imageCover: {
        width: '100%'
    },
    blockText: {
        width: '100%',
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        color: 'white'
    }
}

export default Home